import React, { Component } from "react";
import "./hideBackground.css";

class HideBackground extends Component {
  constructor(props) {
    super(props);
    this.state = {
      style: {
        left: -280,
        transition: "left 0.2s ease 0s"
      },
      maskStyle: {
        display: "none",
        transition: "display 1s ease 0s"
      }
    };
  }

  hideNav = () => {
    this.props.handleClick(this.state.style, this.state.maskStyle);
  };

  state = {};
  render() {
    return (
      <div>
        <div className="mask_header" style={{}} />
        <div
          className="mask_body bgDiv"
          style={this.props.styleValue}
          onClick={this.hideNav}
          //style={{ display: "none", transition: "display 1s ease 0s" }}
        />
      </div>
    );
  }
}

export default HideBackground;
