import React, { Component } from "react";
import "./pictureList.css";
import listItem1 from "../images/img1.jpg";
import listItem2 from "../images/img2.jpg";

const lists = [
  { img: listItem1, title: "私服" },
  { img: listItem2, title: "Cosplay" }
];

class PictureList extends Component {
  state = {};
  render() {
    return (
      <div className="image-list" style={{ margin: "15px 0 0 0" }}>
        <div className="list-title">
          <span className="more">
            <a href="#/">更多</a>
          </span>
          <h2>图片列表模块</h2>
        </div>
        <ul>
          {lists.map((list, index) => {
            return (
              <li key={index}>
                <a href="#/">
                  <img alt="" src={list.img} />
                  <span />
                  <em>{list.title}</em>
                </a>
              </li>
            );
          })}
        </ul>
      </div>
    );
  }
}

export default PictureList;
