import React, { Component } from "react";
import Nav from "./nav";
import NavMenu from "./navMenu";
import Slide from "./slide/slide";
import BottomNavigation from "./bottomNavigation";
import DrawerLayout from "./drawerLayout";
import PictureList from "./pictureList";
import HideBackground from "./hideBackground";
import Publish from "./publish";

class Home extends Component {
  constructor(props) {
    super(props);
    this.state = {
      style: {
        left: -280,
        transition: "left 0.5s ease 0s"
      },
      maskStyle: {
        display: "none",
        transition: "display 1s ease 0s"
      },
      activeStyle: {}
    };
  }

  callBack = (styleValue, maskStyleValue) => {
    this.setState({
      style: styleValue,
      maskStyle: maskStyleValue
    });
  };

  alert = styleValue => {
    this.setState({
      activeStyle: styleValue
    });
  };

  state = {};
  render() {
    return (
      <div>
        <HideBackground
          styleValue={this.state.maskStyle}
          handleClick={this.callBack}
        />
        <Nav handleClick={this.callBack} />
        <NavMenu />
        <Slide />
        <BottomNavigation handleClick={this.alert} />
        <DrawerLayout styleValue={this.state.style} />
        <PictureList />
        <Publish styleValue={this.state.activeStyle} />
      </div>
    );
  }
}

export default Home;
