import React, { Component } from "react";
import "./nav.css";
import ic_search from "../images/ic_search.png";
import ic_drawer_home from "../images/ic_drawer_home.png";

const searchStyle = {
  backgroundImage: "url(" + ic_search + ")"
};
const drawerStyle = {
  backgroundImage: "url(" + ic_drawer_home + ")"
};

class Nav extends Component {
  constructor(props) {
    super(props);
    this.state = {
      // style: {
      //   color: "#000"
      // }
      style: {
        left: 0,
        transition: "left 0.5s ease 0s"
      },
      maskStyle: {
        display: "block",
        transition: "opacity 0.5s ease 0s"
      }
    };

    //Object.assign(this.state, this.props);
    this.showNav = this.showNav.bind(this);
  }
  showNav() {
    //alert("Hello");
    // const style = Object.assign({}, this.state.style, { color: "#fff" });
    // this.setState({
    //   style
    // });
    this.props.handleClick(this.state.style, this.state.maskStyle);
  }

  state = {};
  render() {
    return (
      <div className="navbar bg">
        <a href="#/" className="navigation-link">
          <div className="navigation" style={drawerStyle}>
            <div className="avatar" onClick={this.showNav}>
              <b />
            </div>
          </div>
        </a>
        <a href="#/" className="search" style={searchStyle}>
          {" "}
        </a>
        <span style={this.state.style}>App时代</span>
      </div>
    );
  }
}

export default Nav;
