import React, { Component } from "react";
import "./drawerLayout.css";
import Wave from "./wave";
import Remind from "../images/ic_remind.png";
import Message from "../images/ic_message.png";
import Space from "../images/ic_space.png";
import Setting from "../images/ic_setting.png";

const icons = [
  { img: Remind, name: "提醒" },
  { img: Message, name: "消息" },
  { img: Space, name: "空间" },
  { img: Setting, name: "设置" }
];

class DrawerLayout extends Component {
  constructor(props) {
    super(props);
    this.state = {};
  }

  state = {};
  render() {
    return (
      <div className="drawerLayout" style={this.props.styleValue}>
        <div className="header">
          <div className="left">
            <div className="user">
              <a href="#/">
                <img alt="" />
              </a>
            </div>
          </div>
          <div className="right">
            <div className="slogan">
              <a href="#/">有你更精彩！</a>
            </div>
            <div className="login_register">
              <div className="login">
                <a href="#/" className="login_btn">
                  登录
                </a>
              </div>
              <div className="register">
                <a href="#/" className="register_btn">
                  注册
                </a>
              </div>
            </div>
          </div>
          <Wave />
        </div>
        <div className="menu_bar">
          <ul>
            {icons.map((icon, index) => {
              return (
                <li key={index}>
                  <a href="#/">
                    <img alt="" src={icon.img} />
                    <p>{icon.name}</p>
                  </a>
                </li>
              );
            })}
          </ul>
        </div>
      </div>
    );
  }
}

export default DrawerLayout;
