import React, { Component } from "react";
import "./wave.css";
import Water1 from "../images/water-1.svg";
import Water2 from "../images/water-2.svg";

const waterOneStyle = {
  backgroundImage: "url(" + Water1 + ")"
};

const waterTwoStyle = {
  backgroundImage: "url(" + Water2 + ")"
};

class Wave extends Component {
  state = {};
  render() {
    return (
      <div className="wave">
        <div className="water_1" style={waterOneStyle} />
        <div className="water_2" style={waterTwoStyle} />
      </div>
    );
  }
}

export default Wave;
