import React, { Component } from "react";
import "./bottomNavigation.css";
import "../font/iconfont.css";

class BottomNavigation extends Component {
  constructor(props) {
    super(props);
    this.state = {
      activeStyle: {
        transform: "translateY(0)"
      }
    };
  }

  publish = () => {
    this.props.handleClick(this.state.activeStyle);
  };

  state = {};
  render() {
    return (
      <div>
        <div className="bottom-navigation">
          <a href="#/" className="home selected">
            <i className="iconfont icon-home" />
            <br />
            首页
          </a>
          <a href="#/" className="community">
            <i className="iconfont icon-community" />
            <br />
            社区
          </a>
          <a href="#/" className="publish" onClick={this.publish}>
            <i className="iconfont icon-publish" />
          </a>
          <a href="#/" className="circle">
            <i className="iconfont icon-circle" />
            <br />
            圈子
          </a>
          <a href="#/" className="user-center">
            <i className="iconfont icon-user-center" />
            <br />
            个人中心
          </a>
        </div>
        <div className="publish-circle" />
      </div>
    );
  }
}

export default BottomNavigation;
